import { useEffect, useState } from 'react'
import '../styles/Cart.css'



function Cart({ cart, updateCart }) {
	const [isOpen, setIsOpen] = useState(true)
	const total = cart.reduce(
		(acc, plantType) => acc + plantType.amount * plantType.price,
		0
	)

	//on va appeler Ã§a toujours quand le composant est re-render
	// useEffect(() => {
	// 	document.title = `Ndaho Maison de Jungle`
	// })

	//on va appeler Ã§a uniquement la premiere fois
	useEffect(() => {
		document.title = `Ndaho Maison de Jungle`
	}, [])

	//on va appeler Ã§a pour la premiere fois et quand le total change
	useEffect(() => {
		document.title = `LMJ: ${total}â‚¬ d'achats`
	}, [total])

	//on va appeler Ã§a uniquement quand le composant est demontÃ©( avec un return)
	// useEffect(() => {
	// 	return console.log(`Notre cart est demontÃ©/cachÃ©`);
	// })


	return isOpen ? (
		<div className='lmj-cart'>
			<button
				className='lmj-cart-toggle-button'
				onClick={() => setIsOpen(false)}
			>
				Fermer
            </button>
			{cart.length > 0 ? (
				<div>
					<h2>Panier</h2>
					<ul>
						{cart.map(({ name, price, amount }, index) => (
							<div key={`${name}-${index}`}>
								{name} {price}â‚¬ x {amount}
							</div>
						))}
					</ul>
					<h3>Total :{total}â‚¬</h3>
					<button onClick={() => updateCart([])}>Vider le panier</button>
				</div>
			) : (
					<div>Votre panier est vide</div>
				)}
		</div>
	) : (
			<div className='lmj-cart-closed'>
				<button
					className='lmj-cart-toggle-button'
					onClick={() => setIsOpen(true)}
				>
					Ouvrir le Panier
            </button>
			</div>
		)
}

export default Cart
